body{background-color:#1c1c1c;font-family:'Signika', sans-serif;-webkit-animation:fade-in 1500ms ease-in;animation:fade-in 1500ms ease-in}.content{display:flex;flex-direction:column;background-color:#1c1c1c;width:100%;padding:5vh;box-sizing:border-box;min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content}.site-header{align-self:center;font-size:40px;color:white;margin-bottom:35px}.top-row,.bottom-row{display:flex;justify-content:space-between;height:3vh;margin-bottom:35px}.top-row *,.bottom-row *{border-radius:7px}.left-top-row{display:flex;width:35%;justify-content:space-between}#season-dropdown,#stat-dropdown{width:45%;cursor:pointer;border:none;padding:0px 7px;transition:all 300ms ease-in-out}#season-dropdown:hover,#stat-dropdown:hover{transform:scale(1.05)}.not-selected-error{background-color:#af2a2a;color:#ffe100}.not-selected-error.animation{animation:error-shake 400ms linear;-moz-animation:error-shake 400ms linear;-o-animation:error-shake 400ms linear;-webkit-animation:error-shake 400ms linear}.right-top-row{display:flex;width:5%;min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content}.glossary-button{width:100%;cursor:pointer;border:none;padding:0px 7px;transition:all 300ms ease-in-out}.glossary-button:hover{transform:scale(1.05)}.search-players-input-container{display:flex;flex-direction:column;width:19.5%;margin-right:7vw;position:relative;box-shadow:0px 0px 4px white;height:4vh;align-self:center}#search-players-input{min-height:100%;box-sizing:border-box;border:none;padding:12px;background-color:#1c1c1c;color:white}#player-dropdown{background-color:lightgray;-webkit-animation-name:fade-in;animation-name:fade-in;-webkit-animation-duration:500ms;animation-duration:500ms;z-index:1;cursor:pointer}#player-dropdown li{min-height:4vh;padding:2px 8px;border-bottom:1px solid black;display:flex;align-items:center;overflow:hidden;box-sizing:border-box}.player-names-container{width:75.5%;display:flex;color:white;align-items:center;padding:0 1vw;box-sizing:border-box;height:5vh;align-self:center;justify-content:space-evenly;box-shadow:0px 0px 4px white}@media only screen and (max-device-width: 500px){.player-names-container{font-size:10px}}footer{display:flex;flex-direction:column;height:4vh;background-color:indianred}
