#graph-container{background-color:#070707;margin:0 auto;margin-bottom:50px;height:75vh;border-radius:20px;position:relative;width:100%;color:white}.graph-glow{-webkit-animation:graph-glow 1200ms infinite;animation:graph-glow 1200ms infinite}.line{stroke-width:.40%;fill:none}.x-axis,.y-axis{font:12px sans-serif}.hover-info-container{position:absolute;background-color:rgba(90,87,87,0.8);border-radius:8px;padding:10px;text-align:center;flex-direction:column;font-weight:bold;text-shadow:0px 0px 6px black;font-size:20px}@media only screen and (max-device-width: 500px){.hover-info-container{font-size:10px}}.hover-overlay{background-color:rgba(0,0,0,0.4);pointer-events:all}.text{font-size:20px}@media (max-width: 500px){.text{font-size:10px}}
